.Number-Input {
  background-color: #333333;
  border: none;
  color: white;
  margin: 0 0 1rem 0;
  outline: none;
  padding: 12px 20px;
  -webkit-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
  width: 280px;
  -webkit-appearance: none;
  -moz-appearance: textfield; }
  .Number-Input:focus {
    background-color: #FFAD17; }
  .Number-Input:hover {
    background-color: darkturquoise; }
  .Number-Input::-webkit-input-placeholder {
    color: white; }
  .Number-Input:-ms-input-placeholder {
    color: white; }
  .Number-Input::-ms-input-placeholder {
    color: white; }
  .Number-Input::placeholder {
    color: white; }
  .Number-Input::-webkit-outer-spin-button, .Number-Input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0; }



