.Admin {
  overflow-x: auto; }
  .Admin table {
    border-collapse: separate;
    border-spacing: 0;
    margin: 0;
    padding: 0;
    width: 100%; }
    .Admin table th {
      min-width: 160px;
      text-transform: uppercase; }
    .Admin table th, .Admin table td {
      padding: 10px;
      text-align: center; }
    .Admin table tr:nth-child(even) {
      background: #DDDDDD; }
    .Admin table td img {
      height: 25px;
      padding-right: 5px;
      width: 25px; }
  .Admin.Dark table th {
    background: rgba(255, 255, 255, 0.8);
    color: #333333; }
  .Admin.Light table th {
    background: rgba(0, 0, 0, 0.8);
    color: white; }



